.card {
  width: 2.5in;
  position: relative;
}

.card:before {
  padding-top: 140%;
  content: ' ';
  display: block;
  background-color: white;
}

.card__icon {
  position: absolute;
}

.card__icon:before {
  display: block;
  content: ' ';
}

.card__icon img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: 100%;
  max-height: 100%;
}

.card__img {
  width: 100%;
  -webkit-box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.25);
          box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.25);
  position: absolute;
  top: 0;
  left: 0;
}

.card > * {
  position: absolute;
  font-family: 'Spectral SC', serif;
  border: none;
  background: transparent;

  /* outline: 3px solid rgba(0, 256, 0, 0.3); */
}

.card__title,
.card__type_title,
.card__attunement_title,
.card__attunement_title_yes,
.card__attunement_title_no,
.card__value_unit {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}


.card__title p,
.card__type p,
.card__description p {
  margin: 0;
}

.card__type {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  text-align: left;
  vertical-align: middle;
}

.card__description,
.card__type_title,
.card__attunement_title,
.card__attunement_title_yes,
.card__attunement_title_no,
.card__value_unit {
  font-family: 'Crimson Text', serif !important;
}

.card__description p,
.card__description ul {
  padding-bottom: 0.5em;
  margin: 0;
}

.card__description ul {
  padding-left: 7%;
}

.card__value {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  text-align: center;
  vertical-align: middle;
}

.card__attunement {
  width: 4.20%;
}

.card__attunement:before {
  content: ' ';
  display: block;
  padding-top: 100%;
  background-color: black;
}

/* default */
.card--default .card__title {
  top: 6.25%;
  left: 50%;
  width: 82%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  height: 6.5%;
}

.card--default .card__type {
  top: 18.45%;
  left: 46.5%;
  width: 45.5%;
  max-width: 81%;
  height: 5.5%;
}

.card--default .card__type_title {
  top: 13%;
  left: 27.5%;
  width: 45.5%;
  max-width: 81%;
  height: 5.5%;
}

.card--default .card__description {
  top: 41%;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  width: 87%;
  height: 45.75%;
}

.card--default .card__value {
  top: 26.8%;
  left: 49%;
  width: 12.25%;
  height: 7.5%;
}

.card__attunement_title {
  top: 25%;
  left: 73%;
  width: 45.5%;
  max-width: 20%;
  height: 5.5%;
  word-wrap: break-word;
  line-height: 1em;
}

.card__attunement_title_yes {
  top: 30.1%;
  left: 51%;
  width: 45.5%;
  max-width: 81%;
  height: 5.5%;
}

.card__attunement_title_no {
  top: 30.1%;
  left: 62%;
  width: 45.5%;
  max-width: 81%;
  height: 5.5%;
}

.card--default .card__attunement {
  top: 31.5%;
}

.card--default .card__attunement.false {
  left: 87.5%;
}

.card--default .card__attunement.true {
  left: 77.5%;
}

.card__value_unit {
  top: 28.2%;
  left: 41.5%;
  width: 45.5%;
  max-width: 81%;
  height: 5.5%;
}

.card--default .card__icon {
  position: absolute;
  top: 19%;
  left: 23.5%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  width: 25.5%;
}

.card--default .card__icon:before {
  padding-top: 79%;
}

/* long */
.card--long .card__title {
  top: 6.25%;
  left: 50%;
  width: 82%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  height: 6.5%;
}

.card--long .card__type {
  top: 17.75%;
  left: 9.0%;
  width: 57.5%;
  max-width: 81%;
  height: 5.0%;
}

.card--long .card__description {
  top: 25%;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  width: 87%;
  height: 61.75%;
}

.card--long .card__value {
  display: none;
}

.card--long .card__attunement {
  top: 19.3%;
}

.card--long .card__attunement.false {
  left: 89%;
}

.card--long .card__attunement.true {
  left: 78.5%;
}

.card--long .card__icon {
  display: none;
}

.card--long .card__icon:before {
  padding-top: 79%;
}



